  width: 100vw;
  height: 100vh;
  background-color: aqua;
}

.content-introvid {
  position: sticky;
  top: 0px;
  width: 100vw;
  height: 100%;
  background: rgb(255, 255, 255);
  z-index: 1;
}

.fv {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  object-fit: contain;
  z-index: 10;
}

.content-introvid .bv {
  position: absolute;
  left: 0px;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -o-filter: blur(30px);
  filter: blur(30px);
  opacity: 0.65;
  object-fit: cover;
}

.content-moodoo-container {
  position: relative;
  width: 100vw;
  height: 170vh;
  background-color: rgb(255, 255, 255);
  scroll-snap-type: y mandatory;
}

.content-moodoo {
  position: sticky;
  top: 0px;
  width: 100%;
  height: 100vh;
}

@media (max-width: 1100px) {
  .content-turu-wrap {
    position: absolute;
    left: 50%;
    top: 42%;
    transform: translate(-50%, -50%);
    width: calc(100vw - 80px);
    /* margin-left: 40px; */
    height: 100vh;
    transition: 0.3s all ease;
    z-index: 3;
  }

  .turu_moodoo_it {
    position: absolute;
    top: 55%;
    transform: translate(0%, -50%);
    left: 0%;
    z-index: 10;
  }

  .turu_moodoo_it span {
    font-size: 40px;
    float: left;
    font-weight: 900;
    color: black;
    margin: 5px;
    transition: 0.1s all ease;
  }

  .turu-mooddo-blackbox {
    position: absolute;
    left: 44%;
    top: calc(79% + 5px);
    width: 99px;
    height: 5px;
    background-color: black;
    transition: 0.1s all ease;
  }

  .turu_moodoo_it_itda {
    position: relative;
    left: 100px;
  }

  .reeeddot {
    position: relative;
    width: 8px;
    height: 8px;
    background-color: #ff0000;
    border-radius: 100px;
    left: 265px;
    top: 83px;
  }

  .turu-logo-moodoo {
    position: absolute;
    top: 135%;
  }

  .turu-logo-moodoo-ci {
    width: 190px;
    height: 50px;
    position: relative;
    left: 5px;
    /* margin-right: 10px; */
    object-fit: contain;
    transition: 0.1s all ease;
  }

  .turu-logo-moodoo-type {
    width: 121px;
    transition: 0.1s all ease;
  }

  .part2-bars {
    position: absolute;
    left: 50%;
    top: 63%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 1080px;
    transition: 1s all ease;
    overflow: hidden;
    z-index: 1;
  }

  .part2-bars .yellow {
    width: 120px;
    height: 0px;
    background: linear-gradient(0deg, #fbc400 0%, rgba(251, 196, 0, 0) 100%);
    position: absolute;
    left: 60px;
    top: 0%;
    transform: translate(-50%, 0%);
    border-radius: 0px 0px 1000px 1000px;
    opacity: 0.6;
  }

  .part2-bars .orange {
    width: 200vw;
    height: 120px;
    background: linear-gradient(
      90deg,
      #ff8021 62.57%,
      rgba(255, 128, 33, 0) 112.43%
    );
    border-radius: 1000px;
    overflow: hidden;
    z-index: 1;
    opacity: 0.8;
  }

  .orangem {
    position: absolute;
    top: 60.3%;
    transform: translate(0%, 0%);
    left: calc(100vw);
    display: block;
  }

  .orangepc {
    display: none;
  }

  .redpc {
    display: none;
  }

  .redm {
    position: absolute;
    left: calc(100vw);
    top: 60.3%;
    transform: translate(0%, 0%);
  }

  .part2-bars .red {
    width: 120px;
    height: 120px;
    background: #f15933;
    border-radius: 1000px;
    z-index: 3;
  }

  .part2-bars .yellow2 {
    float: top;
    width: 120px;
    height: 120px;
    background: #fbc400;
    opacity: 0.5;
    border-radius: 1000px;
    z-index: 2;
  }

  .yellowpc {
    display: none;
  }

  .yellowm {
    position: absolute;
    left: calc(100vw);
    top: 60.3%;
    transform: translate(0%, 0%);
  }
}

